@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: "Raleway", sans-serif;
}

/* header css*/
.header-style {
  background-image: url(/static/media/steamworkslogo.f8449860.png);
  background-position: relative;
  background-size: contain;
  background-repeat: no-repeat;
  height: 200px;
}

/* content css */

.title {
  position: relative;
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  padding: 20px;
  font-size: 1.8em;
  border-radius: 5px;
}

.content-body {
  width: 100%;
  justify-content: center;
  padding: auto;
}

.container {
  position: absolute;
  width: 100%;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  padding: 10px;
  box-sizing: border-box;
  top: 0px;
  left: 0px;
  right: 0px;
  margin: auto;
}

/* carousel css */

.whole {
    background-color: rgb(221, 221, 221);
    padding: 30px;
    border-radius: 5px;
}

.description{
    background-color:white;
    margin: 30px;
    padding: 5px;
    border-radius: 5px;
}

.slider {
    width: 80%;
    height: 500px;
  }
  
  .slides {
    position: relative;
  }

  .slides.slick-prev,
  .slides.slick-next {
    position: absolute;
    top: 50%;
    z-index: 1;
  }
  
  .slides.slick-prev {
    left: 5%;
  }
  
  .slides.slick-next {
    right: 5%;
  }
  
  .imageSize {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
.question {
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 5px;
}

.questionBox {
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.2);
  margin-top: 5px;
  padding: 15px;
  box-sizing: border-box;
  background-color: rgb(250, 250, 250);
}

.answerBtn {
  position: relative;
  padding: 10px;
  background-color: rgb(250, 151, 37);
  display: inline-block;
  margin: 10px;
  outline: none;
  border: none;
  font-size: 1em;
  color: rgb(255, 255, 255);
  font-weight: bold;
  border-radius: 4px;
  cursor: pointer;
}

.score-board {
  margin: auto;
  position: relative;
  height: 150px;
}

.score {
  width: 100%;
  font-size: 0.5em;
  top: 100px;
  text-align: center;
}

.playBtn {
  position: absolute;
  font-size: 0.5em;
  padding: 10px;
  background-color: rgb(46, 182, 46);
  color: rgb(255, 255, 255);
  width: 150px;
  left: 0px;
  right: 0px;
  margin: auto;
  outline: none;
  cursor: pointer;
}

body, html, #root, .app, .login{
  width: 100%;
  height: 100%;
}

.login{
  font-size: 28px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.loginForm{
  box-sizing: border-box;
  width: 100%;
  max-width: 400px;
  padding-left: 20px;
  padding-right: 20px;
}

.inputField{
  padding-top: 16px;
}

.input{
  box-sizing: border-box;
  outline: none;
  border: solid 2px #1189de;
  border-radius: 4px;
  color: #292929;
  width: 100%;
  padding: 12px;
  font-size: 14px;
  background: rgba(255, 255, 255, 1);
}

.submitButton{
  padding-top: 16px;
}

.btn{
  width: 100%;
  min-width: 280px;
  color: #565656;
  padding: 12px;
  font-size: 14px;
  font-weight: bold;
  border: solid 2px #1189de;
  border-radius: 4px;
  background: #fff;
  cursor: pointer;
}
